import React from "react";
import {Button, Grid, Layout, Typography, Input, Switch, Statistic} from '@arco-design/web-react';
import GlobalConf from '../conf/EnigmaConf'
import Util from '../util/Util'

const Row = Grid.Row;
const Col = Grid.Col;
const Sider = Layout.Sider;
const Header = Layout.Header;
const Footer = Layout.Footer;
const Content = Layout.Content;
const TextArea = Input.TextArea;

class AbelAes extends React.Component {
    inputValueOnChange;

    encrypt = true;

    constructor(props, context) {
        super(props, context);
        this.state = {
            showValue: "", mode: "加密模式",


        }
    }

    aesValueOnChange = (v) => {
        this.inputValueOnChange = v;
    }

    onTrans = () => {
        if (this.encrypt) {
            let encrypt = Util.AES.encrypt(Util.AesKey, Util.AesIv, this.inputValueOnChange);
            this.setState({showValue: encrypt})
        } else {
            let decrypt = Util.AES.decrypt(Util.AesKey, Util.AesIv, this.inputValueOnChange);
            this.setState({showValue: decrypt})
        }

    }

    keepFormatOnChange = (v) => {
        this.encrypt = !v;
        if (this.encrypt) {
            this.setState({mode: "加密模式"})
        } else {
            this.setState({mode: "解密模式"})
        }
    }

    render() {
        return (<>
                <Layout style={{height: '1200px'}}>
                    <Header style={{textAlign: 'center'}}>
                        <Typography.Title heading={1}>AES</Typography.Title>
                    </Header>

                    <Layout>
                        <Content>
                            <Row style={{width: GlobalConf.Width}} align={'center'}>
                                <Col offset={GlobalConf.Offset} span={2}>
                                    <Statistic value={this.state.mode} groupSeparator style={{marginRight: 60}}/>
                                </Col>
                                <Col span={2}>
                                    <Switch onChange={this.keepFormatOnChange}/>
                                </Col>
                            </Row>

                            <Row style={{width: GlobalConf.Width}} align={'center'}>

                                <Col offset={GlobalConf.Offset} span={6}>

                                    <TextArea placeholder='Please enter ...'
                                              style={{
                                                  minHeight: 200, width: 350, borderColor: "purple", resize: "none"
                                              }}
                                              onChange={this.aesValueOnChange}
                                              autoSize={false}/>

                                </Col>

                                <Col span={2}>
                                    <Button
                                        type='primary'
                                        onClick={this.onTrans}
                                    >Trans</Button>
                                </Col>

                                <Col span={6}>
                                    <TextArea
                                        style={{minHeight: 200, width: 350, borderColor: "purple", resize: "none"}}
                                        value={this.state.showValue}
                                        readOnly={true}/>
                                </Col>
                                <Col span={1} styel={{marginLeft: "2px", marginTop: "100px"}}>
                                </Col>
                            </Row>


                        </Content>
                        <Sider style={{width: "300px"}}>
                        </Sider>

                    </Layout>
                    <Footer>

                    </Footer>
                </Layout>
            </>

        );
    }
}


export default AbelAes;